export class YMapsObjects {
    /** ÐÐ°ÑÑÐ° */
    Map;
    /** ÐÐ°ÑÐºÐµÑÑ */
    markers;
    /** ÐÑÑÑ Ð´Ð¾ Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ñ Ð±Ð°Ð»ÑÐ½Ð° */
    pathToBaloon;
    /** ÐÐ»Ð°ÑÑÐµÑÑ, ÐµÑÐ»Ð¸ Ð½ÑÐ¶Ð½Ñ ÑÐ°Ð·Ð½ÑÐµ Ð²Ð¸Ð´Ñ Ð¼Ð°ÑÐºÐµÑÐ¾Ð² */
    clusters;

    /**
     * @param {{Map: any, markers: Array, pathToBaloon: string, clusters: Array}} params
     */
    constructor(params) {
        this.markers = params.markers;
        this.clusters = params.clusters;
        this.pathToBaloon = params.pathToBaloon
        this.Map = params.Map;
    }

    /** Ð¡Ð¾Ð·Ð´Ð°ÑÑ Ð¼Ð°ÑÐºÐµÑÑ Ð´Ð»Ñ Ð¾Ð´Ð½Ð¾Ð³Ð¾ ÐºÐ»Ð°ÑÑÐµÑÐ° */
    fCreateMarkers(markers, pathToBaloon) {
        const objectManager = new ymaps.ObjectManager({
            // Ð§ÑÐ¾Ð±Ñ Ð¼ÐµÑÐºÐ¸ Ð½Ð°ÑÐ°Ð»Ð¸ ÐºÐ»Ð°ÑÑÐµÑÐ¸Ð·Ð¾Ð²Ð°ÑÑÑÑ, Ð²ÑÑÑÐ°Ð²Ð»ÑÐµÐ¼ Ð¾Ð¿ÑÐ¸Ñ.
            clusterize: true,
            // ÐÐ¿ÑÐ¸Ð¸ Ð´Ð»Ñ ÐºÐ°ÑÑÐ¾Ð¼Ð½Ð¾Ð¹ Ð¸ÐºÐ¾Ð½ÐºÐ¸ Ð¾Ð´Ð¸Ð½Ð¾ÑÐ½Ð¾Ð¹ Ð¼ÐµÑÐºÐ¸
            geoObjectIconLayout: 'default#image',
            // Ð¡Ð²Ð¾Ñ Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ Ð¸ÐºÐ¾Ð½ÐºÐ¸ Ð¼ÐµÑÐºÐ¸.
            geoObjectIconImageHref: pathToBaloon,
            // Ð Ð°Ð·Ð¼ÐµÑÑ Ð¼ÐµÑÐºÐ¸.
            geoObjectIconImageSize: [50, 50],
            // Ð¡Ð¼ÐµÑÐµÐ½Ð¸Ðµ Ð»ÐµÐ²Ð¾Ð³Ð¾ Ð²ÐµÑÑÐ½ÐµÐ³Ð¾ ÑÐ³Ð»Ð° Ð¸ÐºÐ¾Ð½ÐºÐ¸ Ð¾ÑÐ½Ð¾ÑÐ¸ÑÐµÐ»ÑÐ½Ð¾ ÐµÑ "Ð½Ð¾Ð¶ÐºÐ¸" (ÑÐ¾ÑÐºÐ¸ Ð¿ÑÐ¸Ð²ÑÐ·ÐºÐ¸).
            geoObjectIconImageOffset: [-25, -50],
            // ÐÐ¿ÑÐ¸Ð¸ Ð´Ð»Ñ ÐºÐ°ÑÑÐ¾Ð¼Ð½Ð¾Ð¹ Ð¸ÐºÐ¾Ð½ÐºÐ¸ ÐºÐ»Ð°ÑÑÐµÑÐ°
            clusterIconLayout: 'default#image',
            // Ð¡Ð²Ð¾Ñ Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ Ð¸ÐºÐ¾Ð½ÐºÐ¸ Ð¼ÐµÑÐºÐ¸.
            clusterIconImageHref: pathToBaloon,
            // Ð Ð°Ð·Ð¼ÐµÑÑ Ð¼ÐµÑÐºÐ¸.
            clusterIconImageSize: [70, 70],
            // Ð¡Ð¼ÐµÑÐµÐ½Ð¸Ðµ Ð»ÐµÐ²Ð¾Ð³Ð¾ Ð²ÐµÑÑÐ½ÐµÐ³Ð¾ ÑÐ³Ð»Ð° Ð¸ÐºÐ¾Ð½ÐºÐ¸ Ð¾ÑÐ½Ð¾ÑÐ¸ÑÐµÐ»ÑÐ½Ð¾
            // ÐµÑ "Ð½Ð¾Ð¶ÐºÐ¸" (ÑÐ¾ÑÐºÐ¸ Ð¿ÑÐ¸Ð²ÑÐ·ÐºÐ¸).
            clusterIconImageOffset: [-35, -70],
        });

        const objectColection = [];

        for (let i = 0; i < markers.length; i++) {
            objectColection.push({
                type: 'Feature',
                id: markers[i].id,
                geometry: {
                    type: 'Point',
                    coordinates: [markers[i].latitude, markers[i].longitude]
                },
            })
        }
        objectManager.add(objectColection);
        this.Map.geoObjects.add(objectManager);
    }

    /** Ð¡Ð¾Ð·Ð´Ð°ÑÑ Ð¼Ð½Ð¾Ð¶ÐµÑÑÐ²Ð¾ ÐºÐ»Ð°ÑÑÐµÑÐ¾Ð² */
    fCreateMultipleClusters(clusters) {
        for (let i = 0; i<clusters.length; i++) {
            this.fCreateMarkers(clusters.markers, clusters.pathToBaloon)
        }
    }

    /** Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ Ð¾Ð±ÑÐµÐºÑ ÐºÐ¾Ð½ÑÑÐ¾Ð»Ð°, Ñ Ð¿Ð¾Ð¼Ð¾ÑÑÑ templateLayoutFactory */
    fCreate() {
        this.Map.geoObjects.removeAll();

        let objectManager
        if (this.clusters && this.clusters.length) {
            objectManager = this.fCreateMultipleClusters(this.clusters)
        } else {
            objectManager = this.fCreateMarkers(this.markers, this.pathToBaloon)
        }

        return this.objectManager;
    }
}
